import React from "react";
import "./Sidebar.css";
import logo from "./logo.svg";
import icon1 from "./icon1.svg";
import icon2 from "./icon2.svg";
import icon3 from "./icon3.svg";
import icon4 from "./icon4.svg";

// GitHub Pages HashRouter URL  
const menuItems = [
  {
    label: "HOME",
    children: [
      {
        label: "",
        icon: icon1,
        url: "https://kjh011012.github.io/wms_project_frontend/manager/dashboard/#/admin/Mainpage",
        external: true,
      },
    ],
  },
  {
    label: "  ",
    children: [
      {
        label: " ",
        icon: icon1,
        url: "https://kjh011012.github.io/wms_project_frontend/manager/inbound/#/admin/contract-status",
        external: true,
      },
      {
        label: " ",
        icon: icon1,
        url: "https://kjh011012.github.io/wms_project_frontend/manager/inbound/#/admin/inbound-status-detail",
        external: true,
      },
    ],
  },
  {
    label: "",
    children: [
      {
        label: " ",
        icon: icon2,
        url: "https://kjh011012.github.io/wms_project_frontend/manager/outbound/#/admin/OutboundStatus",
        external: true,
      },
    ],
  },
  {
    label: "",
    children: [
      {
        label: " ",
        icon: icon2,
        url: "https://kjh011012.github.io/wms_project_frontend/manager/inventory/#/admin/InventoryStatus",
        external: true,
      },
    ],
  },
  {
    label: "",
    children: [
      {
        label: " ",
        icon: icon2,
        url: "https://kjh011012.github.io/wms_project_frontend/manager/equipments/#/admin/EquipmentList",
        external: true,
      },
    ],
  },
  {
    label: "",
    children: [
      {
        label: "",
        icon: icon3,
        url: "https://kjh011012.github.io/wms_project_frontend/manager/inquiry/#/admin/Notices",
        external: true,
      },
      {
        label: "",
        icon: icon4,
        url: "https://kjh011012.github.io/wms_project_frontend/manager/inquiry/#/admin/Inquiries",
        external: true,
      },
      {
        label: "",
        icon: icon4,
        url: "https://kjh011012.github.io/wms_project_frontend/manager/inquiry/#/admin/Employees",
        external: true,
      },
    ],
  },
];

// Sidebar 
const Sidebar = () => {
  return (
    <div className="sidebar">
      <div className="sidebar-header">
        <img src={logo} alt="Logo" className="sidebar-logo" />
        Smart WMS
      </div>

      {menuItems.map((menu, index) => (
        <div key={index} className="menu-group">
          <div className="menu-label">{menu.label}</div>
          <div className="menu-children">
            {menu.children.map((child, childIndex) => (
              <a
                key={childIndex}
                href={child.url}
                className="sidebar-item"
                target="_self"
                rel="noopener noreferrer"
              >
                {child.icon && (
                  <img
                    className="sidebar-icon"
                    src={child.icon}
                    alt={child.label}
                  />
                )}
                <span>{child.label}</span>
              </a>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

export default Sidebar;
