import React, { useState, useEffect, useRef } from "react";
//import axios from "axios";
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/styles/ag-grid.css";
import "ag-grid-community/styles/ag-theme-alpine.css";
//import "../components/Notices.css";
import mockNotices from "./mock/mockNotices";

//const API_BASE_URL = "http://34.47.73.162:5000/api/notices";

function NoticeBoard() {
  const [rowData, setRowData] = useState([]);
  const [selectedNotice, setSelectedNotice] = useState(null);
  const [formData, setFormData] = useState({ title: "", content: "", author: "", date: "" });
  const [showDetailPanel, setShowDetailPanel] = useState(false);
  const [showNewPage, setShowNewPage] = useState(false);
  const [searchText, setSearchText] = useState("");
  const [message, setMessage] = useState(null); //  
  const gridRef = useRef(null);

  //  
  const columnDefs = [
    { headerName: "ID", field: "id", width: 80, sortable: true },
    { headerName: "", field: "title", flex: 1, sortable: true, filter: true },
    { headerName: "", field: "author", width: 150, sortable: true, filter: true },
    { headerName: "", field: "date", width: 150, sortable: true, filter: true },
  ];

  //   
  useEffect(() => {
    fetchNotices();
  }, [searchText]);

  const fetchNotices = () => {
  let data = [...mockNotices];

  if (searchText.trim()) {
    const keyword = searchText.toLowerCase();
    data = data.filter(
      (notice) =>
        notice.title.toLowerCase().includes(keyword) ||
        notice.author.toLowerCase().includes(keyword) ||
        notice.content.toLowerCase().includes(keyword)
    );
  }

  setRowData(data);
};


  // handleInputChange 
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  //    
  const getCurrentDateTime = () => {
    const now = new Date();
    return now.toISOString().slice(0, 19).replace("T", " ");
  };

  //   
  const showMessage = (text, type = "success") => {
    setMessage({ text, type });
    setTimeout(() => setMessage(null), 3000); // 3    
  };

  //  
  const handleSave = () => {
  const updatedFormData = { ...formData, date: getCurrentDateTime() };

  if (showNewPage) {
    const newId = mockNotices.length > 0 ? Math.max(...mockNotices.map(n => n.id)) + 1 : 1;
    mockNotices.push({ ...updatedFormData, id: newId });
    showMessage("  .", "success");
  } else if (selectedNotice) {
    const index = mockNotices.findIndex(n => n.id === selectedNotice.id);
    if (index !== -1) {
      mockNotices[index] = { ...selectedNotice, ...updatedFormData };
      showMessage("  .", "success");
    }
  }

  fetchNotices();
  closePanels();
};


  //  
  const handleDelete = () => {
  if (selectedNotice) {
    mockNotices = mockNotices.filter(n => n.id !== selectedNotice.id);
    showMessage(" .", "success");
    fetchNotices();
    closePanels();
  }
};


  //  
  const onRowClicked = (event) => {
    setSelectedNotice(event.data);
    setFormData(event.data);
    setShowDetailPanel(true);
    setShowNewPage(false);
  };

  //    
  const openNewPage = () => {
    setFormData({ title: "", content: "", author: "", date: "" });
    setShowNewPage(true);
    setShowDetailPanel(false);
  };

  //  
  const closePanels = () => {
    setShowDetailPanel(false);
    setShowNewPage(false);
    setSelectedNotice(null);
    setFormData({ title: "", content: "", author: "", date: "" });
  };

  return (
    <div style={{

      width:"95%",
      height:"80%",
      padding:"10px",
      marginLeft:"20px",
      marginTop:"30px", 
      display:"flex",
      borderRadius:"10px",
      backgroundColor:"#FFFFFF",
      flexDirection:"column",
      boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)", //  
    background: "linear-gradient(135deg, #ffffff, #ffffff)", //  
    }}>
      <h3 style={{marginLeft:"5px"}}></h3>
      <div style={{border:"1px solid #6f47c5",
        width:"97%",
        marginBottom:"15px",
        marginLeft:"5px"
        }}></div>
    <div style={{
      width:"100%",
      height:"100%",
      backgroundColor:"#ffffff",
      boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)", //  
    background: "linear-gradient(135deg, #ffffff, #ffffff)", //  
      }}>
      {/*   */}
      {message && (
        <div className={`alert ${message.type}`}>
          {message.text}
        </div>
      )}
      <div style={{width:"100%",
         display:"flex",
         justifyContent:"center",
         alignItems:"center",
         }}>
      {/*   */}
        <input
        style={{width:"80%",padding:"8px 12px", fontSize:"13px",borderRadius:"5px",border: "1px solid #ccc",}}
          type="text"
          placeholder=" ..."
          value={searchText}
          onChange={(e) => setSearchText(e.target.value)}
        />
        {/*   */}
<button
  onClick={fetchNotices}
  style={{
    padding: "8px 15px",
    marginLeft:"10px",
    backgroundColor: "#28a745",
    color: "white",
    border: "none",
    borderRadius: "6px",
    fontSize: "14px",
    cursor: "pointer",
    boxShadow: "0 2px 4px rgba(0, 0, 0, 0.2)",
    transition: "background-color 0.3s ease, transform 0.2s ease",
  }}
  
>
  
</button>

      
    
      {/*   */}
<button
  onClick={openNewPage}
  style={{
    padding: "8px 15px",
    backgroundColor: "#007bff",
    marginLeft:"10px",
    color: "white",
    border: "none",
    borderRadius: "6px",
    fontSize: "14px",
    cursor: "pointer",
    boxShadow: "0 2px 4px rgba(0, 0, 0, 0.2)",
    transition: "background-color 0.3s ease, transform 0.2s ease",
  }}
 
>
  
</button>

      </div>
     {/* AG Grid  */}
{/* AG Grid  */}
<div
  className="ag-theme-alpine grid-container"
  style={{
    height: "400px", //   
    width: "100%", //    
    overflow: "hidden", //    
  }}
>
  <AgGridReact
    ref={gridRef}
    rowData={rowData}
    columnDefs={columnDefs}
    rowSelection="single"
    onRowClicked={onRowClicked}
    pagination={true} //   
    paginationPageSize={10} //     
    domLayout="normal" //   
    suppressPaginationPanel={false} //    
  />
</div>


      </div>


 
     {/*    */}
{(showDetailPanel || showNewPage) && (
  <div
    style={{
      position: "fixed",
      top: 0,
      left: 0,
      width: "100%",
      height: "100%",
      backgroundColor: "rgba(0, 0, 0, 0.5)", //   
      zIndex: 1000, //    
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    }}
    onClick={closePanels}
  >
    <div
      style={{
        backgroundColor: "white",
        borderRadius: "10px",
        padding: "20px",
        width: "50%",
        maxHeight: "80%",
        overflowY: "auto",
        position: "relative",
        zIndex: 1000, //    
      }}
      onClick={(e) => e.stopPropagation()}
    >
      <button
        style={{
          position: "absolute",
          top: "10px",
          right: "10px",
          background: "none",
          border: "none",
          fontSize: "18px",
          cursor: "pointer",
          color: "#888",
        }}
        onClick={closePanels}
      >
        
      </button>
      {showNewPage ? (
        <div>
          <h3 style={{ textAlign: "center", marginBottom: "20px" }}> </h3>
          <label style={{ display: "block", marginBottom: "10px", fontWeight: "bold" }}></label>
          <input
            type="text"
            name="title"
            value={formData.title}
            onChange={handleInputChange}
            placeholder=""
            style={{
              width: "100%",
              padding: "10px",
              marginBottom: "15px",
              borderRadius: "5px",
              border: "1px solid #ddd",
            }}
          />
          <label style={{ display: "block", marginBottom: "10px", fontWeight: "bold" }}></label>
          <textarea
            name="content"
            value={formData.content}
            onChange={handleInputChange}
            placeholder=" "
            style={{
              width: "100%",
              height: "100px",
              padding: "10px",
              borderRadius: "5px",
              border: "1px solid #ddd",
              marginBottom: "15px",
            }}
          />
          <label style={{ display: "block", marginBottom: "10px", fontWeight: "bold" }}></label>
          <input
            type="text"
            name="author"
            value={formData.author}
            onChange={handleInputChange}
            placeholder=""
            style={{
              width: "100%",
              padding: "10px",
              marginBottom: "15px",
              borderRadius: "5px",
              border: "1px solid #ddd",
            }}
          />
          <button
            onClick={handleSave}
            style={{
              padding: "10px 20px",
              backgroundColor: "#007bff",
              color: "white",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
              marginRight: "10px",
            }}
          >
            
          </button>
        </div>
      ) : (
        <div>
          <h3 style={{ textAlign: "center", marginBottom: "20px" }}> </h3>
          <label style={{ display: "block", marginBottom: "10px", fontWeight: "bold" }}></label>
          <input
            type="text"
            name="title"
            value={formData.title}
            onChange={handleInputChange}
            placeholder=""
            style={{
              width: "100%",
              padding: "10px",
              marginBottom: "15px",
              borderRadius: "5px",
              border: "1px solid #ddd",
            }}
          />
          <label style={{ display: "block", marginBottom: "10px", fontWeight: "bold" }}></label>
          <textarea
            name="content"
            value={formData.content}
            onChange={handleInputChange}
            placeholder=" "
            style={{
              width: "100%",
              height: "100px",
              padding: "10px",
              borderRadius: "5px",
              border: "1px solid #ddd",
              marginBottom: "15px",
            }}
          />
          <label style={{ display: "block", marginBottom: "10px", fontWeight: "bold" }}></label>
          <input
            type="text"
            name="author"
            value={formData.author}
            onChange={handleInputChange}
            placeholder=""
            style={{
              width: "100%",
              padding: "10px",
              marginBottom: "15px",
              borderRadius: "5px",
              border: "1px solid #ddd",
            }}
          />
          <p>
            <strong>:</strong> {formData.date}
          </p>
          <button
            onClick={handleSave}
            style={{
              padding: "10px 20px",
              backgroundColor: "#007bff",
              color: "white",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
              marginRight: "10px", 
            }}
          >
            
          </button>
          <button
            onClick={handleDelete}
            style={{
              padding: "10px 20px",
              backgroundColor: "#dc3545",
              color: "white",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
            }}
          >
            
          </button>
        </div>
      )}
    </div>
  </div>
)}

    </div>
  );
}

export default NoticeBoard;
