import React, { useState, useEffect } from 'react';
//import axios from 'axios';
import '../components/EmployeeManagement.css';
import mockEmployees from './mock/mockEmployees';


const EmployeeManagement = () => {
  const [employees, setEmployees] = useState([]); //   
  const [selectedEmployee, setSelectedEmployee] = useState(null); //   
  const [isModalOpen, setIsModalOpen] = useState(false); //  / 
  const [isEditing, setIsEditing] = useState(false); //   
  const [editForm, setEditForm] = useState({}); //    

  //const API_URL = process.env.REACT_APP_API_URL || 'http://34.47.73.162:5002'; // API URL 

  useEffect(() => {
  const fetchEmployees = () => {
    const formattedEmployees = mockEmployees.map(employee => ({
      ...employee,
      created_at: employee.created_at || new Date().toLocaleDateString('ko-KR'),
    }));
    setEmployees(formattedEmployees);
  };

  fetchEmployees();
}, []);


  const handleEmployeeClick = (id) => {
    if (selectedEmployee && selectedEmployee.id === id) {
      setIsModalOpen(!isModalOpen);
    } else {
      const employee = employees.find((emp) => emp.id === id);
      setSelectedEmployee(employee);
      setIsModalOpen(true);
      setIsEditing(false); //   
    }
  };

  const handleDelete = (employeeId) => {
  const confirmDelete = window.confirm(" ?");
  if (!confirmDelete) return;

  const index = mockEmployees.findIndex(emp => emp.id === employeeId);
  if (index !== -1) {
    mockEmployees.splice(index, 1); // mock 
    setEmployees(mockEmployees);
    setIsModalOpen(false);
  }
};



  const handleEditClick = () => {
    setIsEditing(true);
    setEditForm({ ...selectedEmployee });
  };

  const handleEditChange = (e) => {
    const { name, value } = e.target;
    setEditForm((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSaveEdit = () => {
  const index = mockEmployees.findIndex(emp => emp.id === editForm.id);
  if (index !== -1) {
    mockEmployees[index] = { ...editForm };
    setEmployees([...mockEmployees]);
    setSelectedEmployee(editForm);
    setIsEditing(false);
  }
};


  const closeModal = () => {
    setIsModalOpen(false);
    setIsEditing(false);
  };

  const handleOutsideClick = (e) => {
    if (e.target.classList.contains('modal-overlay')) {
      closeModal();
    }
  };

  return (
    <div className="main-container">
      <div className="employee-list-container">
        <h2> </h2>
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {employees.map((employee, index) => (
              <tr key={employee.id} onClick={() => handleEmployeeClick(employee.id)}>
                <td>{index + 1}</td>
                <td>{employee.username}</td>
                <td>{employee.email}</td>
                <td>{employee.phone_number}</td>
                <td>{employee.created_at}</td>
                <td>{employee.department}</td>
                <td>{employee.position}</td>
                <td>{employee.salary}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {isModalOpen && selectedEmployee && (
        <div className="modal-overlay" onClick={handleOutsideClick}>
          <div className="employee-detail-container" onClick={(e) => e.stopPropagation()}>
            {isEditing ? (
              <>
                <h2> </h2>
                <form>
                  <div>
                    <label>:</label>
                    <input
                      type="text"
                      name="username"
                      value={editForm.username}
                      onChange={handleEditChange}
                    />
                  </div>
                  <div>
                    <label>:</label>
                    <input
                      type="email"
                      name="email"
                      value={editForm.email}
                      onChange={handleEditChange}
                    />
                  </div>
                  <div>
                    <label>:</label>
                    <input
                      type="text"
                      name="employee_id"
                      value={editForm.employee_id}
                      onChange={handleEditChange}
                    />
                  </div>
                  <div>
                    <label>:</label>
                    <input
                      type="text"
                      name="phone_number"
                      value={editForm.phone_number}
                      onChange={handleEditChange}
                    />
                  </div>
                  <div>
                    <label>:</label>
                    <input
                      type="text"
                      name="address"
                      value={editForm.address}
                      onChange={handleEditChange}
                    />
                  </div>
                  <div>
                    <label>:</label>
                    <input
                      type="text"
                      name="department"
                      value={editForm.department}
                      onChange={handleEditChange}
                    />
                  </div>
                  <div>
                    <label>:</label>
                    <input
                      type="text"
                      name="position"
                      value={editForm.position}
                      onChange={handleEditChange}
                    />
                  </div>
                  <div>
                    <label>:</label>
                    <input
                      type="text"
                      name="salary"
                      value={editForm.salary}
                      onChange={handleEditChange}
                    />
                  </div>
                  <div className="button-container">
                    <button className="add-button" onClick={handleSaveEdit}></button>
                    <button className="delete-button" onClick={() => setIsEditing(false)}></button>
                  </div>
                </form>
              </>
            ) : (
              <>
                <h2> </h2>
                <p><label>:</label> {selectedEmployee.username}</p>
                <p><label>:</label> {selectedEmployee.email}</p>
                <p><label>:</label> {selectedEmployee.employee_id || "N/A"}</p>
                <p><label>:</label> {selectedEmployee.phone_number}</p>
                <p><label>:</label> {selectedEmployee.address || "N/A"}</p>
                <p><label>:</label> {selectedEmployee.department}</p>
                <p><label>:</label> {selectedEmployee.position}</p>
                <p><label>:</label> {selectedEmployee.salary || "N/A"}</p>

                <div className="button-container">
                  <button className="add-button" onClick={handleEditClick}></button>
                  <button className="delete-button" onClick={() => handleDelete(selectedEmployee.id)}></button>
                </div>
              </>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default EmployeeManagement;
